
import { defineComponent, onMounted, ref , computed , watch} from 'vue';
import { useRouter } from 'vue-router';
export default defineComponent({
  props:{
    sceneData: {
      type: Object,
      default: {}
    },
  },
  setup({sceneData}){
    onMounted(()=>{
      //localididtrue
      if(localStorage.getItem(sceneData.ScenicSpotID)){
        collected.value = true
      }else{
        collected.value = false
      }
    })
    const router = useRouter()
    const localStorage = window.localStorage
    const collected =ref(false)
    const defaultPicture = require('../../assets/images/icon.png')
    function selected(category:string,id:string,data:any) {  //
      collected.value = !collected.value
      if(collected.value == true){
        localStorage.setItem(id,JSON.stringify(data))
        if(!localStorage.getItem(`${category}CollectList`)){ //
          //
          localStorage.setItem(`${category}CollectList`,`${id}#`) //ID
        }else{  //
          //ID
             let temp = localStorage.getItem(`${category}CollectList`) as string
             temp = `${temp}${id}#`
             localStorage.setItem(`${category}CollectList`,temp)
        }
      }else{
        localStorage.removeItem(id)
        if(localStorage.getItem(`${category}CollectList`)){ //ID
          let temp = localStorage.getItem(`${category}CollectList`) as string
          let dataList = temp.split('#')
          // console.log("",dataList)
          temp = "" //0
          dataList.find((i,index)=>{ //ID
          console.log(i)
            if(i ===id){
              // console.log("ID")
              dataList[index] = ""
            }
          })
          // console.log("",dataList)
          dataList.forEach((i:string)=>{ //
            if(i!==""){
              temp+=`${i}#`
            }
          }) 
          // console.log("",temp)
          localStorage.setItem(`${category}CollectList`,temp)
        }
      }
    }
    function gotItemPage (data:any) {
      let temp = JSON.stringify(data)
      router.push({
        path:`/ItemDisplay`,
        query:{
          data:temp
        }
      })
    }
    return{
      //data
      collected,defaultPicture,
      //mathods
      selected,gotItemPage,
    }
  }
});
